* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.main-container {
  text-align: center;
}
.menu-container {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}
.menu-section {
margin-left: 20px;
margin-bottom: 20px;
}

.image {
  width: 200px;
  height: 100px;
  border: 1px solid gold;
}
.header-para {
  width: 250px;
  
}
.menu-header {
  display: flex;
  justify-content: space-between;
  width: 200px;
  padding-top: 10px;
}
.header-para {
  font-size: 9px;
  text-align: left;
  width: 200px;
  padding-top: 10px;
}

.price {
  color: rgb(161, 139, 10);
}
.btn {
  border: none;
  margin-left: 10px;
  color: rgb(161, 139, 10);
  background: none;
  margin-bottom: 20px;
  cursor: pointer;
}
.btn:hover {
  color:black;
}

.main-title {
  position: sticky;
  top: 0;
  background: grey;
  padding: 10px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.365);
  margin-bottom: 20px;
  
}
/* Media query for other screen sizes*/

@media  screen and (min-width: 481px){
  .menu-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

}}